.homeClass {
    width: 50%;

    background-color: #F9EAEA;
    margin: auto;
    text-align: center;
    border-radius: 50px;
    padding: 20px;
}

.homeClass button {
    width: 120px;
    height: 40px;
    border-radius: 60px;
    background-color: #c33;
    border-style: none;
    color: #F9EAEA;
    margin: 10px;
}

.homeClass button:hover {
    background-color: #A32828;
}